import axios from 'axios'

export const getAllPlants = async () => {
    return axios.get("https://mg-gardencentre.herokuapp.com/api/plants/");
}

export const getPlant = (id) => {
    return axios.get(`https://mg-gardencentre.herokuapp.com/api/plants/${id}`);
}

export const putPlant=(id,plant)=>{
    return axios.put(`https://mg-gardencentre.herokuapp.com/api/plants/${id}`,plant)
}

export const deletePlant=(id)=>{
    return axios.delete(`https://mg-gardencentre.herokuapp.com/api/plants/${id}`);
}

export const createPlant=(plant)=>{
    return axios.post(`https://mg-gardencentre.herokuapp.com/api/plants/`,plant);
}

export const getAllOrders = async () => {
    return axios.get("https://mg-gardencentre.herokuapp.com/api/orders/");
}

export const getOrder = (id) => {
    return axios.get(`https://mg-gardencentre.herokuapp.com/api/orders/${id}`);
}

export const putOrder=(id,order)=>{
    return axios.put(`https://mg-gardencentre.herokuapp.com/api/orders/${id}`,order)
}

export const deleteOrder=(id)=>{
    return axios.delete(`https://mg-gardencentre.herokuapp.com/api/orders/${id}`);
}

export const createOrder=(order)=>{
    return axios.post(`https://mg-gardencentre.herokuapp.com/api/orders/`,order);
}


export default {
    getAllPlants,getPlant,putPlant,deletePlant,createPlant,getAllOrders,getOrder,putOrder,deleteOrder,createOrder
}