import React, { useState } from 'react';
;
const useFieldEditor = (className,type="text",initialValue="") => {
    let [value,setValue] = useState(initialValue);

    const inputField = <input type={type} className={className} onChange={(event)=>setValue(event.target.value)} value={value} />
    return {
        value,inputField
    }

}

export default useFieldEditor;